*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}



.game-letter{
    font-weight: 900;
    font-size: 1rem;
    padding: 1rem;
    margin:0.5rem;
    background: linear-gradient(rgba(125, 218, 20, 0.7), rgba(3, 219, 86, 0.7));
}
.game-letter:disabled{  
    color: black;  
    background: linear-gradient(rgba(141, 148, 133, 0.7), rgba(49, 59, 53, 0.7));
    box-shadow: 2px 2px 3px black;
}